.india-covid-status-container {
  display: grid;
  grid-template-columns: .5fr;
  margin: 2rem;
}
.india-covid-status {
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: 6rem;
  column-gap: .5rem;
  .status-grid-container{
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: .5rem;
    border-radius: .4rem;
    border: 1px solid black;
    justify-content: center;
  }
  .status-label{
    font-weight: 600;
  }
  .status-center{
    float: right;
  }
}